'use client';
import { motion } from 'framer-motion';
import { Button } from '@/Component/UI/TableFormat';
import { FiCalendar, FiEye, FiEdit2, FiTrash2 } from 'react-icons/fi';
import DateRange from '@/utilities/DateRange';

const EventCard = ({
  event,
  onView,
  onEdit,
  onDelete
}) => {
  const handleAction = (e, action) => {
    e.stopPropagation();
    action(event);
  };

  return (
    <motion.article
      initial={{ opacity: 0, y: 10 }}
      animate={{ opacity: 1, y: 0 }}
      whileHover={{
        y: -4,
        boxShadow: '0 10px 25px -5px rgba(0, 0, 0, 0.1)'
      }}
      transition={{
        type: 'spring',
        stiffness: 300,
        damping: 20
      }}
      className="flex flex-col h-full bg-white rounded-xl shadow-sm border border-gray-100 overflow-hidden transition-all duration-200 hover:shadow-md"
      onClick={() => onView(event)}
      aria-label={`Event: ${event.title}`}
    >
      {/* Header with gradient background */}
      <div className="relative h-40 bg-gradient-to-br from-indigo-50 to-purple-50 flex items-center justify-center p-6">
        <h3 className="text-xl font-semibold text-gray-800 text-center line-clamp-2">
          {event.title}
        </h3>
      </div>

      {/* Content area */}
      <div className="flex flex-col flex-grow p-5">
        {/* Date information */}
        <div className="flex items-center text-gray-500 mb-4">
          <FiCalendar className="w-5 h-5 mr-2 flex-shrink-0" />
          <DateRange
            start={event.dates.start}
            end={event.dates.end}
            className="text-sm font-medium"
          />
        </div>

        {/* Action buttons */}
        <div className="mt-auto flex justify-end space-x-2">
          <Button
            size="sm"
            variant="outline"
            icon={<FiEye className="w-4 h-4" />}
            onClick={(e) => handleAction(e, onView)}
          >
            View
          </Button>
          <Button
            size="sm"
            variant="primary"
            onClick={(e) => handleAction(e, onEdit)}
            aria-label="Edit event"
            icon={<FiEdit2 className="w-4 h-4" />}
          >
            Edit
          </Button>
          <Button
            size="sm"
            variant="danger"
            onClick={(e) => handleAction(e, onDelete)}
            aria-label="Delete event"
            icon={<FiTrash2 className="w-4 h-4" />}
          >
            Delete
          </Button>
        </div>
      </div>
    </motion.article>
  );
};

export default EventCard;